(function() {
  'use strict';

  angular
    .module('app', ['ngResource', 'ui.router', 'toastr']);

})();

(function() {
  'use strict';

  angular
    .module('app')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creation" is avaible by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();



(function() {
  'use strict';

  angular
    .module('app')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($http) {
    var vm = this;

    vm.latest_alerts = [];
    vm.temperatures = [];

    $http.get('/api/alerts').success(function(data) {
      vm.latest_alerts = data.reverse();
    });

    var getData = function() {
      $http.get('/api/temperatures').success(function(result) {
        if(result.length > 0) {
          if(vm.temperatures.length > 10) {
            vm.temperatures.pop();
          }
          angular.forEach(result, function(x) {
            vm.temperatures.push({
              deviceid: x.deviceid._,
              temperature: x.temperaturereading._,
              eventtime:  x.eventtime._
            });
            vm.temperatures.reverse();
          });
          vm.latest_reading = vm.temperatures[0];
        }
      });
    }

    getData();
    setInterval(getData, 5000)

  }
})();

(function() {
  'use strict';

  angular
    .module('app')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('app')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('settings', {
        url: '/settings',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('app')
    .constant('malarkey', malarkey)
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('app')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

angular.module("app").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div class=\"container\"><div><acme-navbar></acme-navbar></div><div class=\"row\"><div class=\"col-md-4\"><h2>Current Temperature</h2>The last recorded reading on device {{ main.latest_reading.deviceid }} was:<table><thead><tr><th>Temperature</th><th>Time</th></tr></thead><tbody><tr><td>{{ main.latest_reading.temperature | number }}&deg;C</td><td>{{ main.latest_reading.eventtime | date : \'medium\' }}</td></tr></tbody></table></div><div class=\"col-md-4\" style=\"overflow-wrap:break-word\"><h2>Alerts</h2><pre ng-repeat=\"alert in latest_alerts\">{{ main.alert | json }}</pre><br></div><div class=\"col-md-4\"><h2>Temperature History</h2><div ng-show=\"main.temperatures.length > 0\"><h4>Most recent {{ main.temperatures.length }} readings</h4><table><thead><tr><th>Temperature</th><th>Time</th></tr></thead><tbody><tr ng-repeat=\"t in main.temperatures\"><td>{{ t.temperature | number }}&deg;C</td><td>{{ t.eventtime | date : \'medium\' }}</td></tr></tbody></table><div ng-show=\"main.temperatures.length === 0\">No temperature history found</div></div></div></div></div>");
$templateCache.put("app/components/navbar/navbar.html","<nav class=\"navbar navbar-static-top navbar-inverse\"><div class=\"container-fluid\"><div class=\"navbar-header\"><a class=\"navbar-brand\" href=\"https://github.com/Swiip/generator-gulp-angular\"><span class=\"glyphicon glyphicon-home\"></span> Gulp Angular</a></div><div class=\"collapse navbar-collapse\" id=\"bs-example-navbar-collapse-6\"><ul class=\"nav navbar-nav\"><li class=\"active\"><a ng-href=\"#\">Home</a></li><li><a ng-href=\"#\">About</a></li><li><a ng-href=\"#\">Contact</a></li></ul><ul class=\"nav navbar-nav navbar-right acme-navbar-text\"><li>Application was created {{ vm.relativeDate }}.</li></ul></div></div></nav>");}]);